body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  text-align: center;
  font-weight: 100;
  font-size: 38px;
  letter-spacing: 3px;
  font-family: 'Courier New', Courier, monospace;
}

.disclaimer {
  margin: 10px 0;
  font-size: 12px;
  color: #666;
  font-weight: 100;
  text-align: center;
}


.header {
  background: black;
  text-align: center;
}
.header > img {
  padding: 2vh 30vw;
  width: 10vw;
  height: 4vh;
}
.car {
  width: 100%;
  min-height: 350px;
  margin-top: -30px;
  background:  #fff url(/tesla-range-calculator/static/media/tesla.ff16203f.jpg) top center no-repeat;
  background-size: contain;
}

.car-wheels {
  position: relative;
  height: 247px;
  width: 555px;
  margin: 0 auto;
}

.car-wheel {
  position: absolute;
  width: 80px;
  height: 80px;
  bottom: 0;
  background-position: 0 0;
  background-size: contain;
  background-repeat: no-repeat;
}

.car-front-wheel {
  left: 53px;
}

.car-rear-wheel {
  right: 72px;
}

.wheel19 {
  background-image: url(/tesla-range-calculator/static/media/wheel-19.3b975a00.png)
}

.wheel21 {
  background: url(/tesla-range-calculator/static/media/wheel-21.e787dc92.png)
}
.display-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: -40px;
}

.display-panel > div {
  width: 150px;
}
.model-icon {
  height: 20px;
  background-size: auto 15px;
  background-position: top right;
  background-repeat: no-repeat;
}
.model-60 {
  background-image: url(/tesla-range-calculator/static/media/60.7ee66eea.svg)
}
.model-60D {
  background-image: url(/tesla-range-calculator/static/media/60d.246b5ba5.svg)
}
.model-75 {
  background-image: url(/tesla-range-calculator/static/media/75.37862a62.svg)
}
.model-75D {
  background-image: url(/tesla-range-calculator/static/media/75d.9c937cb9.svg)
}
.model-90D {
  background-image: url(/tesla-range-calculator/static/media/90d.28a215f0.svg)
}
.model-P100D {
  background-image: url(/tesla-range-calculator/static/media/p100d.62ed87b4.svg)
}

.display-panel > div > p {
  margin-top: -2px;
  font-size: 38px;
  font-weight: normal;
  color: #008cffe3;
  text-align: right;
}

.display-panel > div > p > sup{
  font-size: 14px;
  font-weight: normal;
}
.control-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom:50px;
}

.control-panel > div {
  width: 200px;
  text-align: center;
}

.option-container p, .wheel-box p {
  font-size: 25px;
}
.option-container sup, .wheel-box sup {
  font-size: 12px;
}

.option-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border: 1px solid #ccc;
  margin: 0px 10px;
  background-color: #f7f7f7;
}

.option-button-container {
  display: grid;
  grid-auto-flow: row;
}

.option-button-container .increase {
  background-image: url(/tesla-range-calculator/static/media/up.b3fc73bf.svg);
  border-bottom: 1px solid #ccc;
}

.option-button-container .decrease {
  background-image: url(/tesla-range-calculator/static/media/down.a56566ba.svg);
}

.option-button-container > button {
  outline: 0;
  width: 30px;
  color: #008dff;
  cursor: pointer;
  padding: 11px 0;
  vertical-align: middle;
  border: 0;
  background-size: 60%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
}

.speed-control {
  border-right: 1px solid #ccc;
}

.climate-control {
  padding-left: 60px;
  margin-top: 20px;
}
.climate-button {
  width: 120px;
  height: 120px;
  border: 7px solid rgb(231, 229, 229);
  border-radius: 50%;
  color: #666;
  background: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
}

.climate-button.AC.on {
  background: #39f;
  color:white;
}

.climate-button.HEAT.on {
  background: red;
  color:white;
}

.climate-icon {
  width: 22px;
  margin: 8px auto 0;
  height: 22px
}
.climate-icon.AC.on {
  background: url(/tesla-range-calculator/static/media/ac-on.4dcccadf.svg);
}

.climate-icon.AC.off {
  background: url(/tesla-range-calculator/static/media/ac-off.c418a045.svg);
}

.climate-icon.HEAT.on {
  background: url(/tesla-range-calculator/static/media/heat-on.2c61b1a6.svg);
}

.climate-icon.HEAT.off {
  background: url(/tesla-range-calculator/static/media/heat-off.987170ea.svg);
}

.wheel-option-container {
  display: -webkit-flex;
  display: flex;
}

.wheel-21 {
  background: url(/tesla-range-calculator/static/media/21.a52693ae.svg)
}

.wheel-19 {
  background: url(/tesla-range-calculator/static/media/19.c8ef3398.svg)
}

.wheel-control {
  width: 360px !important;
  margin-left: -20px;
}

.wheel-box {
  width: 170px;
  border: 1px solid #ccc;
  margin: 0 8px 0 0;
  background-color: #f7f7f7;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.wheel-box > div {
  width: 40px;
  height: 40px;
  padding: 0 20px;
  margin-top: 20px;
  bottom: 0;
  background-position: 10px 0;
  background-size: contain;
  background-repeat: no-repeat;
}

.wheel-box > p {
  padding: 0 10px;
}

.wheel-box.active {
  border-color: #39f;
}
